<template>
	<div>
	    <!--头部-->
	    <header class="common-header ">
	        <a  href="javascript:history.back(-1)" class="header-left returnLeft" >返回</a>
	        <h4>登陆/注册</h4>
	    </header>
		<!--登录注册入口样式-->
		<div class="wrapperEntry">
	        <img src="../../../static/images/icon/logo.jpg" class="wrapperEntry-icon"/>
			<div class="wrapperLogin-login pAll15">
	            <span class="common-input fl" @click="showPage(1)">
					登录
				</span>
	            <span class="common-input fr " @click="showPage(2)">
					注册
				</span>
	        </div>
		</div>

		<!--登录模块-->
		<login :message="showClass" v-on:loginEvent="hideLogin"></login> 	
		<!--注册模块-->
		<transition name="fade">
			<register v-if="show" v-on:registerEvent="hideRegister"></register>
		</transition>
		
	</div>
</template>

<style scoped>
	/*** ======= 入口样式  BEGIN  =======***/ 
	.wrapperEntry{
		width: 100%;
	    height: 100%;
	    z-index: 100;
	}
	.wrapperEntry .wrapperLogin-login{
		position: absolute;
		bottom: 10%;
		left: 0;
		width: 100%;
		box-sizing: border-box;
	}
	.wrapperEntry span{
		background: #36b70a;
		color: #fff;
	}
	.wrapperEntry-icon{
		display: block;
		width: 300px;
		margin:30px auto;
	}
	.wrapperLogin-login{
		margin-top:30px;
		color: #252525;
		font-size:14px;
	}
	.wrapperLogin-login span{
		width: 48%;
		text-align: center;
		display: inline-block;
	}
	/*** ======= 入口样式  END  =======***/ 

   .fade-enter-active, .fade-leave-active {
        transition: all .2s ease-out;
        transform: translateX(0);
 	}
    .fade-enter, .fade-leave-active {
         transform: translateX(100%)
    }
</style>

<script>
	import Login 	from './login'
	import Register from './Register'
	export default{
		data(){
			return {
				show:false,
				showClass:""
			}
		},
		components:{
			Login,Register
		},
		methods:{
			showPage(type){
				if(type == 2){
					this.show = true;
				}else{
					this.showClass = "on"
				}
			},
			hideLogin(){
				this.showClass = ""
			},
			hideRegister(state){
				console.log(111);
				this.show = state;
			}
		}

	}
</script>


