<template>
	<div>
  	<!--头部-->
	  <header class="common-header">
	    <a  @click="history" class="header-left returnLeft">返回</a> 
	    <h4>消息列表</h4>
	    <span class="header-right">清空</span>
	  </header>
	    
	  <section>
	    <ul class="messageList fz16">
	      <li class="theme-green">通知</li>
	      <li>私信</li>
	    </ul>
	    <div class="noMessagePrice"></div>
	    <p class="theme-gray fz16 center">暂无消息</p>
	  </section>
	</div>


</template>


<style>
	html,body{
    background: #eee;
  }
</style>

<style scoped>
  .messageList{
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #fff;
    text-align: center;
  }
  .messageList li{
    width: 50%;
    float: left;
  }
  .noMessagePrice{
    display: block;
    width: 104px;
    height: 104px;
    background-size: 100% auto;
    background-image: url(../../../static/images/icon/no_content.png);
    background-position: 0 -724px;
    margin:100px auto 30px;
  }
</style>

<script>
    export default {
        methods:{
          history(){
              this.$router.go(-1);
          }
        }
    }
</script>


