<template>
<div>
	<!--头部-->
	<header class="common-header ">
		<a  @click="history" class="header-left returnLeft">返回</a>	
		<h4>个人中心</h4>
	</header>

	<section>
	  	<div class="pt20 headerMain">
	  		<img src="../../../static/images/icon/tool.png" class="fr tool pr20"/>
	  		<div class="cl"></div>
			<div class="avatar avatar-man mt20">
				<img src="../../../static/images/avatar/3.jpeg" />
			</div>
			<p class="fz18 mt10">杨老湿</p>
			<div class="pt15 pb10 theme-blue-4c9afa fz14">
				<span>0关注</span>
				<span>0粉丝</span>
			</div>
			<p class="theme-blue-4c9afa fz14">这家伙很懒，什么都没留下</p>
			<ul class="nav J_nav" >
				<li :class="{actvie:isShow == 1}"  @click="showTab(1)">动态</li>
				<li :class="{actvie:isShow == 2}" @click="showTab(2)">兴趣</li>
			</ul>
	  	</div>
  	</section>
  	<!--动态-->
	<section class="userTrends plr15 pt20 J_section" v-show="isShow == 1">
		<div class="userTrends-shop pt10">
			<span class="fz14 fl">最新动态</span>
			<ul class="shop-allTrends fr" :class="{active:isActive}" >
				<li class="J_allTrends" @click="showDynamic"> <span>全部动态</span></li>
				<li >全部动态</li>
				<li >帖子</li>
				<li >评论</li>
				<li >书单</li>
				<li >书评</li>
			</ul>
		</div>
		<div class="userTrends-reportDeatil">
			<img src="../../../static/images/icon/txt.png" />
			<p class="fz12">没有发表任何内容</p>
		</div>
	</section>
	<!--兴趣-->
	<section class="userInterest plr20 mt10  J_section" v-show="isShow == 2 ">
		 <div class="userInterest-circle">
		 	<span class="text-ellipsis fz14">关注的圈子</span>
		 	<span >全部圈子</span>
		 </div>
		 <ul class="userInterest-circleList mt15">
		 	<li>
		 		<img src="../../../static/images/avatar/3.jpeg" />
		 		<p class="text-ellipsis">软软其实不太硬圈</p>
		 	</li>
		 	<li>
		 		<img src="../../../static/images/avatar/3.jpeg" />
		 		<p class="text-ellipsis">软软其实不太硬圈</p>
		 	</li>
		 	<li>
		 		<img src="../../../static/images/avatar/3.jpeg" />
		 		<p class="text-ellipsis">软软其实不太硬圈</p>
		 	</li>
		 	<li>
		 		<img src="../../../static/images/avatar/3.jpeg" />
		 		<p class="text-ellipsis">软软其实不太硬圈</p>
		 	</li>

		 </ul>
	</section>
	</div>
</template>

<style>
	html,body{
		background: #fafafa;
	}
</style>
<style scoped>

	.headerMain{
		background: #fff;
		text-align: center;
		border-bottom: 1px solid #e5e5e5;
	}
	.tool{
		width: 22px;
		height: 22px;
	}
	.avatar{
		position: relative;
		width: 80px;
		height: 80px;
		margin:0 auto;
	}
	.avatar img{
		display: block;
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}
	.avatar-man:after{
		content: "";
		position: absolute;
		right: 0;
		bottom: 0;
		width: 20px;
		height: 20px;
		background: #fff url('../../../static/images/icon/man.png') no-repeat  center;
		background-size: 12px 12px;
		border-radius: 50%;
	}
	.nav{
		margin-top:6px;
		overflow: hidden;
	}
	.nav li{
		position:relative;
		float: left;
		width: 50%;
		height: 34px;
		text-align: center;
		line-height: 34px;
		font-size: 16px;
	}
	.nav li.actvie{
		color: #4c9afa;
	}
	.nav li.actvie:after{
		content: "";
		position: absolute;
		bottom: 0;
		left: 50%;
		margin-left: -32px;
		width: 64px;
		height: 2px;
		background-color: #4c9afa;
	}


	/***=======  动态样式 BEGIN =======***/

	.userTrends{
		text-align: center;
	}

	.userTrends-shop{
		overflow: hidden;
	}
	.userTrends-shop span{
		height: 40px;
		line-height: 40px;
	}	
	.shop-allTrends{
		width: 85px;
		color: #666;
		border:1px solid #fafafa;
		box-sizing: border-box;
		overflow: hidden;
	}

	.shop-allTrends li{
		width: 100%;
		text-align: center;
		height: 40px;
		line-height: 40px;
		display: none;
	}
	.shop-allTrends li:first-child{
		display: block;
		position: relative;
		text-align: left;
		padding-left: 8px;
		box-sizing: border-box;
	}
	.shop-allTrends li:nth-child(2){
		color: #4c9afa;
	}

	.shop-allTrends li:first-child:after{
	    content: "";
	    position: absolute;
	    right: 12px;
	    top: 15px;
	    width: 6px;
	    height: 6px;
	    border-right: 1px solid #666;
	    border-top: 1px solid #666;
	    transform: rotate(132deg);
	    transition: all .2s ease-out;
	}


	.shop-allTrends.active{
		background: #fff;
		border:1px solid #eaeaea;
	}
	.shop-allTrends.active li{display: block;}
	.shop-allTrends.active li:first-child{
		border-bottom: 1px solid #e5e5e5;
	}
	.shop-allTrends.active li:first-child:after{
		 top:19px;
		 transform: rotate(-44deg);
	}

	.userTrends-reportDeatil{
		position: absolute;
		bottom: 26%;
		left: 0;
		width: 100%;
		text-align: center;
	}

	.userTrends-reportDeatil img{
		width: 38px;
		height: 40px;
		display: block;
		margin:0 auto 15px;
	}

	/***=======  动态样式 END =======***/



	/***=======  兴趣样式 BEGIN =======***/
	.userInterest{
		padding-bottom: 20px !important;
		background: #fff;
		overflow: hidden;
	}
	.userInterest-circle{
		width: 100%;
		height: 44px;
		line-height: 44px;
		border-bottom: 1px solid #e5e5e5;
	}
	.userInterest-circle span{
		float: left;
		width: 50%;
	}
	.userInterest-circle span:last-child{
		position: relative;
		text-align: right;
		padding-right: 15px;
		box-sizing: border-box;
		color: #666;
		font-size:12px;
	}
	.userInterest-circle span:last-child:after{
	    content: "";
	    position: absolute;
	    right: 5px;
	    top: 20px;
	    width: 6px;
	    height: 6px;
	    border-right: 1px solid #666;
	    border-top: 1px solid #666;
	    transform: rotate(45deg);
	}

	.userInterest-circleList{

	}
	.userInterest-circleList li{
		text-align: center;
		float: left;
		width: 25%;
		padding:0 10px;
		box-sizing: border-box;
	}
	.userInterest-circleList li img{
		width: 50px;
		height: 50px;
		display: block;
		margin:0 auto 10px;
		border-radius: 4px;
	}

	/***=======  兴趣样式 END =======***/
</style>
<script>
 export default {
     data () {
		return {
			isActive:false,
			isShow:1
		}
	 },
	 methods : {
		showDynamic : function() {
			this.isActive = !this.isActive
		},
		showTab : function(type) {
			this.isShow = type
		},
		history(){
              this.$router.go(-1);
      	}
	}
 }
</script>


