import Vue from 'vue'
import Router from 'vue-router'
import UserCenter from '@/components/user/user-center'
import UserDetail from '@/components/user/user-detail'
import UserMessage from '@/components/user/user-message'
import UserPublish from '@/components/user/user-publish'
import Main from '@/components/login/main'
Vue.use(Router)

export default new Router({
  routes: [
  	//个人中心页
    {
      path: '/user',
      name: 'UserCenter',
      component: UserCenter
    },
    //个人中心详情页
    {
      path: '/userDetail',
      name: 'userDetail',
      component: UserDetail
    },
    //个人中心消息列表页
    {
      path: '/userMessage',
      name: 'userMessage',
      component: UserMessage
    },
    //个人中心发表列表页
    {
      path: '/userPublish',
      name: 'userPublish',
      component: UserPublish
    },
    {
      path:'/main',
      component:Main
    }
  
  ]
})



