<template>
	<div>
		<!--头部-->
		<header class="common-header">
			<a  @click="history" class="header-left returnLeft">返回</a>	
			<h4>我</h4>
		</header>
		<section class="mt10">
			<router-link to="UserDetail">
				<div class="pAll15 commonArea">
					<img src="../../../static/images/avatar/3.jpeg" class="mr10"/>
					<span class="fz18">杨老湿</span>
				</div>
			</router-link>
			<ol class="userList pl10 mt10">
				<router-link to="userMessage"><li>
					<i class="iconfont icon-xiaoxi  fz20 theme-green"></i>
					<span>消息</span>
				</li></router-link>
				<router-link to="userPublish"><li>
					<i class="iconfont icon-fabiao  fz20 theme-green"></i>
					<span>发表</span>
				</li></router-link>
			</ol>
		</section>
	</div>

</template>
<style>
	body{
		background: #eee;
	}
</style>
<style scoped>
	.commonArea{
		position: relative;
		line-height: 64px;
	    color: #000;
		background: #fff;
		overflow: hidden;
	}
	.commonArea img{
		display: inline-block;
		width: 64px;
		height: 64px;
		border-radius: 50%;
	}
	.commonArea:after{
	    content: "";
	    position: absolute;
	    right: 20px;
	    top: 48px;
	    width: 7px;
	    height: 7px;
	    border: solid #8f8f8f;
	    border-width: 2px 2px 0 0;
	    -webkit-transform: rotate(45deg);
	    transform: rotate(45deg);
	}

	.userList{
	    background: #fff;
	    box-sizing: border-box;
	}
	.userList li{
	    position: relative;
	    width: 100%;
	    height: 44px;
	    line-height: 44px;
	    border-bottom: 1px solid #e5e5e5;
	}
	.userList li:nth-child(2){border-bottom: 0;}
	.userList li:after{
	    content: "";
	    position: absolute;
	    right: 20px;
	    top: 18px;
	    width: 7px;
	    height: 7px;
	    border: solid #8f8f8f;
	    border-width: 2px 2px 0 0;
	    -webkit-transform: rotate(45deg);
	    transform: rotate(45deg);
	}
	.userList li i,.userList li span{
	    vertical-align: middle;
	    padding-right: 4px;
	   
	}
	.userList li span{
	    font-size: 14px;
	    color: #000;
	}

</style>

<script>
    export default {
        methods:{
          history(){
              this.$router.go(-1);
          }
        }
    }
</script>


